<template>
  <div style="max-width: 100%;box-sizing: border-box" class="financialApplyDetail">
    <myHeader :isBack="true" title="申请详情"></myHeader>
    <div class="seat-head"></div>

    <div class="list-block">
      <mt-cell title="养户：" value="某某养户"></mt-cell>
      <mt-cell title="申请日期：">
        默认带出
      </mt-cell>
      <mt-cell title="申请类型：" class="apply-container">
        <div class="apply-purchase">
          <img v-if="isApplyPurchase" @click="switchApplyHandler('isApplyPurchase')" class="select-btn" src="../../../assets/select-icon.png">
          <img v-else class="select-btn" @click="switchApplyHandler('isApplyPurchase')" src="../../../assets/noSelect-icon.png">
          <span>申购</span>
        </div>

        <div class="apply-redeem">
          <img v-if="isApplyRedeem" @click="switchApplyHandler('isApplyRedeem')" class="select-btn" src="../../../assets/select-icon.png">
          <img v-else @click="switchApplyHandler('isApplyRedeem')" class="select-btn" src="../../../assets/noSelect-icon.png">
          <span>赎回</span>
        </div>
      </mt-cell>
      <div v-if="isApplyPurchase">
        <mt-cell title="可用结算金额：" value="217999.00元"></mt-cell>
        <mt-cell title="本次申购金额：" value="3410.00元"></mt-cell>
      </div>
      <div v-if="isApplyRedeem">
        <mt-cell title="可赎回金额：" value="217999.00元"></mt-cell>
        <mt-cell title="本次赎回金额：" value="3410.00元"></mt-cell>
      </div>
    </div>

    <Signature></Signature>

    <div class="takeCare-buy">
      <img @click="agreeReadHandler" v-if="isAgreeRead" class="select-btn" src="../../../assets/select-icon.png">
      <img @click="agreeReadHandler" v-else class="select-btn" src="../../../assets/noSelect-icon.png">
      本人已阅读<span class="mark-text">购买须知</span>内容
    </div>

    <SubmitBtn></SubmitBtn>

  </div>
</template>

<script>
  import myHeader from '@/components/Header'
  import Signature from '@/components/Signature'
  import SubmitBtn from "../../../components/SubmitBtn";

  export default {
    name: 'financialApplyDetail',
    data() {
      return {
        isApplyPurchase: true,
        isApplyRedeem: false,
        isAgreeRead: false
      }
    },
    components: {
      SubmitBtn,
      myHeader,
      Signature
    },
    methods: {
      submitPageData() {

      },
      switchApplyHandler(type) {
        if (type === 'isApplyPurchase') {
          if (!this.isApplyPurchase) {
            this.isApplyPurchase = true;
            this.isApplyRedeem = false;
          }
        } else if (type === 'isApplyRedeem') {
          if (!this.isApplyRedeem) {
            this.isApplyRedeem = true;
            this.isApplyPurchase = false;
          }
        }
      },
      agreeReadHandler() {
        this.isAgreeRead = !this.isAgreeRead;
      }
    }
  }
</script>

<style lang="scss">
  .financialApplyDetail {
    min-height: 100vh;
    background-color: #F8F8F8;

    .head {
      background-color: #FFF;
    }

    .list-block {
      font-size: .16rem;
      margin-top: .1rem;
      background-color: #fff;

      .img {
        width: .1rem;
        height: .06rem;
        margin-left: .08rem;
        background-image: url('~@/assets/icon-next.png');
        background-size: 100%;
      }
    }

    .apply-container {
      .apply-purchase {
        padding-right: .15rem;
      }
      .select-btn {
        margin-right: .06rem;
      }
    }

    .write-info {
      position: relative;
      padding: .15rem;
      margin-top: .1rem;
      font-size: .16rem;
      background-color: #fff;

      .label-name {
        position: absolute;
        left: .15rem;
        top: .15rem;
        color: #000;
      }
    }

    .takeCare-buy {
      font-size: .14rem;
      margin: .3rem .16rem;
      text-align: center;

      .select-btn {
        width: .16rem;
        height: .16rem;
        vertical-align: middle;
        margin-right: .1rem;
      }

      .mark-text {
        color: #FF6A1D;
      }
    }

  }
</style>



